.app__header{
    flex:1;
    width: 100vw;
    height: 100vh;

    flex-direction: row;
    align-items: center;

    padding: 40rem 0rem 0;
}

.app__header-badge{
    border: 0px solid red;
    margin: 0;
    position: absolute;
    top: 55%;
    left: 43%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

.animated_typing{
    font-size: 4vw;
    font-family:'Aboreto' ;
}

.info__button{
    font-weight: 800;
    font-family: 'Aboreto';
    letter-spacing: 0.8px;
    padding: 8px 10px;
    background: linear-gradient(90.21deg, rgba(170, 54, 124, 0.3) , rgba(74, 47, 180, 0.2) 111.58%); 
    border-radius: .4rem;
  

    font-size: 20px;
    margin-bottom: 16px;
    display: inline-block;

    position: absolute;
    top: 80%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    width: 30%;
    transition: all 0.2s ease-in-out;
}

.info__button:hover{
    scale: 1.02;
    color:white
}
@media screen and (max-width: 900px) {
    .app__header{
        height: 100vh;
    }

    .app__header-badge{
        top: 50%;
    }
    .info__button{
        top: 70%;
    }
    .animated_typing{
        font-size: 5vh;
    }
}