.app__team{
    width: 100%;
    padding-top: 10rem;
    padding-bottom: 3rem;
}
.app___team-title{
    color: white;
    background: rgba(26, 147, 207, .7);
    font-family: var(--font-alt);
    font-size: 5vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 3rem;
    padding-right: 2rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    margin-bottom: 3rem;
    /* font-family:'Aboreto' */
}
.team__socials{
    z-index: 10;
}
.back-home{
    font-size: large;
}
.photos{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 3rem;
}
.first__row{
    display: flex;
    justify-content: space-evenly;
    width: 100%;

}

.photo{
    width: 20vw;
    height: auto;
    cursor: pointer;
}
.card__back{
    cursor: pointer;
    background: rgba(250,250,250,.25);
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: var(--secondary-color);
    width: 20vw;
    height: 20.3vw;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: space-between;
    padding-top: 2rem;
    padding-left: .5rem;
    padding-right: .5rem;
    padding-bottom: 2rem;
    font-size: larger;
    flex-direction: column;

}

@media screen and (max-width: 900px) {
    .photo{
        width: 40vw;
        height: auto;
        cursor: pointer;

    }
    .card__back{

        width: 40vw;
        height: 40.5vw;
        font-size: medium;
    }
  
    .elif{
        margin-bottom: 3rem;
    }
    .atakan{
        margin-bottom: 3rem;
    }
    .tuÄŸba{
        margin-bottom: 3rem;
    }
    .first__row{

        margin-bottom: 0;

    }
}
@media screen and (min-width: 900px) {
    .back-home{
        display: none;
    }
}