#events{

  background: url(../../assets/eventsbck.jpg);
  background-size: cover;
  background-repeat: repeat;
  background-position: center;


}
/* .banner{
  width: 100%;
  height: 1rem;
  background: var(--secondary-color);
} */

.app__events{

  /* height: 100vh; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding-bottom: 4rem;

}
.card {
    width: 300px;
    overflow: hidden;
    box-shadow: 0 2px 20 grey ;
    border-radius: 1rem;
    border: 1px solid white;
    display: flex;
    background: white;
    flex-direction: column;
    justify-content: space-between;
    cursor: pointer;
    transition: transform 200ms ease-in;
    margin-bottom: 1rem ;

}
.card__image {
      height: 170px;
      width: 100%;
      object-fit: cover;
    }
  
.card__title {
      padding: 1rem;
      background: white;
    }
  
.card__description {
      padding: 0 1rem;
      background: white;
    }
  
.card__btn {
      padding: 1rem;
      font-family: inherit;
      font-weight: bold;
      font-size: 1rem;
      margin: 1rem;
      border: 2px solid var(--secondary-color);
      background: white;
      color: var(--secondary-color) ;
      border-radius: 1rem;
      transition: background 200ms ease-in, color 200ms ease-in;
      cursor: pointer;
    }
  
.card:hover {
      transform: scale(1.02);
      cursor: pointer;
    }
  
.card__btn:hover {
      background: var(--secondary-color);
      color: white;
     
  }

  .events__title{
    color: white;
    background: rgba(26, 147, 207, .7);
    font-weight:400;
    font-family: var(--font-alt);
    font-size: 5vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 3rem;
    padding-right: 2rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    margin-bottom: 1rem;
    margin-top: 1.5rem;
  }

  .titlerow{
    width: 100%;

    text-align: right;
    height: max-content;
    margin-top: 1rem;
    margin-bottom: 2rem;
    font-family:  var(--font-alt);
    font-size: 2vw;
    /* -webkit-text-stroke: .01px black; */
  }


  @media screen and (min-width: 1000px) {

  .first__row{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-bottom: 1.3rem;

  }
  .second__row{
    display: flex;
    flex-direction: row;
  }
  .app__events{
    height: 100vh;
  }
  }

 
.learn__button{
  
    font-weight: 800;
    font-family: 'Aboreto';
    letter-spacing: 0.8px;
    padding: 8px 10px;
    border-radius: .5rem;
    width: 30%;
    transition: all 0.2s ease-in-out;
    cursor: pointer;
    padding: 1rem;
    font-family: inherit;
    font-weight: bold;
    font-size: 1rem;
    color: white;
    background: var(--secondary-color) ;
    cursor: pointer;
}

.learn__button:hover{
  scale: 1.01;
   background: white;
   color: var(--secondary-color);
}

@media screen and (max-width: 1000px){
  .first__row{
    flex-direction: column;
    align-items: center;
    margin-bottom: 2rem;
  }

  .learn__button{
    width: 300px;
  }
  .titlerow{
    font-size: larger;
  }
}