.app__allevents {

    margin: auto;
    padding-top: 10rem;
    padding-bottom: 3rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 3rem;
}

._event {
    display: flex;
    background: rgba(250, 250, 250, 0.3);
    width: 95%;
    padding-top: 1rem;
    padding-bottom: 1rem;
    justify-content: space-evenly;
    border-radius: .3rem;

}

.description {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    width: 70%;
    text-align: justify;
    background: rgba(26, 147, 207, .1);
    padding: 1rem;
    border-radius: .3rem;


}

.event-img {
    width: 25%;
    height: auto;
    border-radius: .3rem;
}

@media screen and (max-width: 1300px) {
    ._event {
        flex-direction: column;
        gap: 1rem;

        justify-content: center;
        align-items: center;
    }

    .event-img {
        width: 60%;
    }

    .description {
        width: 90%;
    }
}