.app__footer{
    width: 100%;
    height: 60vh;
    background: black;

    display: flex;
    justify-content: space-around;
    align-items: center;
    color: white;
}

.footer__logo{
        width: 30%;
}
.footer__acmlogo{
    width: 100%;
}

.footer__title{
    margin-bottom: 1rem;
}
.footer__info{
    margin-bottom: 1rem;
}

.footer__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.footer-link svg{
    color: var(--secondary-color);
    height: 30px;
    width: 30px;
}

@media screen and (min-width: 900px){
    .footer__acmlogo{
        width: 80%;
    }
    .app__footer{
        padding: 0rem 15rem;
    }
}