.app__navbar{
    width: 100%;
    display: flex;

    justify-content: space-between;
    align-items: center;

    padding: 1rem 2rem;
    position: fixed;
   
    z-index: 10;


}


.app__navbar-links{
    list-style: none;
    display: flex;
    flex: 1;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-right: 2rem;
    margin-left: 2rem;


  
  
}

.navlink{
    /* background: rgba(26, 147, 207,0.8); */
    border-radius: .5rem;
    padding: .5rem;
    color: black;
    transition: all 0.1s ease-in-out;
}


.app__navbar-links li{
    text-transform: uppercase;
    font-weight: 500;
    backdrop-filter: blur(1px);
    -webkit-backdrop-filter: blur(1px);
    
  
}
.small{
    width: 10px;
}
.app__navbar-links a{
    transition: all 0.3s ease-in-out;
} 
.navlink:hover{
    /* background:rgba(26, 147, 207, .9);  */
    color: white;
    scale: 1.01;

}

.app__navbar-menu{


    width: 35px;
    height: 35px;
    border-radius: 50%;

    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--secondary-color);
    transition: all 0.3s ease-in-out;
    margin-right: 0.7rem ;
}

.app__navbar-menu:hover{
    background: white;
}
.app__navbar-menu svg{
    color: white;
    width: 70%;
    height: 70%;
    transition: all 0.6s ease-in-out;
}
.app__navbar-menu svg:hover{
    color:var(--secondary-color);
    
}
@media screen and (min-width: 900px){
    .app__navbar-menu{
        display: none;
    }
    
}
.app__navbar-menu div{
    position: fixed;
    top:0;
    bottom: 0;
    right: 0;
    z-index: 5;
    padding: 1rem;
    width: 80%;
    height: 100vw;
    background-color: var(--secondary-color); 



    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    align-items: flex-end;

    box-shadow: 0 0 20px rgba(168,168,168, 0.15);
    

}
.app__navbar-menu div svg{
    color: white;
    width: 35px;
    height: 35px;
    margin: .3rem 1rem;
}

.app__navbar-menu ul{
        list-style: none;
        padding: 0;
        margin: 0;
        height: 100%;
        width: 100%;

        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
}
.app__navbar-menu li{
    margin: 1rem;

}

.app__navbar-menu li a {
    text-decoration: none;
    font-size: 1rem;
    text-transform: uppercase;
    font-weight: 500;
    transition: all 0.3s ease-in-out;

}

.app__navbar-menu li a:hover{
    color: white;
 
}
.app__logo{
    display: flex;
    justify-content: flex-start;
    align-items: center;


}

.app__logo-small img{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 20%;
}
.app__logo img{
    width: 200px;
}

.app__socials{
    display: flex;
    flex-direction: column-reverse;

}
.app__socials-active{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.app__socials-link svg{

    color: var(--secondary-color);
    height: 30px;
    width: 30px;
    margin: 0 .5rem;
}

.app__navbar-active{
    /* margin-top: 1rem; */
    width: 100%;
    background: var(--secondary-color);
    display: flex;
    /* border: 1px solid (255, 255, 255); */
    justify-content: space-between;
    align-items: center;
    /* background: rgba(255, 255, 255, 0.05); */
    padding: 1rem 2rem;
    position: fixed;
    /* backdrop-filter: blur(1px);
    -webkit-backdrop-filter: blur(1px); */
    z-index: 10;

}


@media screen and (max-width: 900px) {
    
    .app__navbar-links{
        display:none;
    }
    .app__socials{
      display: none;
    }
    .app__navbar-menu{
        width: 35px;
        height: 35px;
        
    }
    .app__navbar-active{
        width: 100%;
        height: 6rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-direction: row-reverse;
        background: none;
        padding: 1rem 2rem;
        position: fixed;
        z-index: 10;
    }
    .app__navbar-menu div{
        height: 110vh;
       
    }

 
}
.app__logo-small{
    display: none;
}
.app__socials-active{
    display: none;
}
.menubuttons{
    cursor: pointer;
}


